





































































































































































































































import { Component, Mixins } from 'vue-property-decorator'
import getRepository from '@/services'
import ContractRepository from '@/services/repositories/contract'
import EmployeeRepository from '@/services/repositories/employee'
import UserRepository from '@/services/repositories/user'
import ParamRepository from '@/services/repositories/params'
import PopupMixin from '@/mixins/popup'
import DistrcitMixin from '@/mixins/commonMixin'
import { ParamsFilter } from '../../interface'
import FilterMixin from '@/mixins/filterAuditContractMixin'
import { debounce, filter, mapValues, trim } from 'lodash'
import { namespace } from 'vuex-class'
import LocationRepository from '@/services/repositories/location'
const beAudit = namespace('beAudit')

const apiUser: UserRepository = getRepository('user')
@Component({ components: {} })
export default class FilterSalary extends Mixins(PopupMixin, FilterMixin, DistrcitMixin) {
  @beAudit.State('filters') filters!: Array<Record<string, any>>

  @beAudit.Action('setReportFilter') setReportFilter!: (data: { contract: string; filters: Record<string, any> }) => void
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }

  api!: ContractRepository
  // apiUser!: UserRepository
  locationRes: LocationRepository | undefined = undefined
  apiEmployee!: EmployeeRepository
  apiParam!: ParamRepository
  members: any = []
  data: Record<string, any> = {}
  isLoading = false
  value1 = []
  valueSelect = []
  valueTest = null
  value = ''
  input = ''
  currentContractYear = ''
  currentFeeYear = ''
  numOfContractYear = ''
  isActive = true
  isCaret = true
  isRight = true
  options = []
  option = []
  listOptions = []
  selectOptions = []
  dataSearch: any
  params = {
    type: 0,
    districtId: this.user?.districts?.[0].id
  }
  listError: Record<string, boolean> = {
    numOfContract: false,
    numOfCancel: false,
    revenue: false,
    salarySalesAmount: false,
    arrearsAmount: false,
    salaryTotalAmount: false,
    sesultMonthRevenue: false,
    value: false
  }
  dataForm: ParamsFilter = {
    areaIds: [],
    branchIds: [],
    transOfficeIds: [],
    salesMemberIds: [],
    month: new Date().getMonth() + 1,
    year: new Date().getFullYear() + '',
    pageIndex: 1,
    pageSize: 20,
    fromNumOfContract: '',
    toNumOfContract: '',
    fromNumOfCancel: '',
    toNumOfCancel: '',
    fromResultMonthRevenue: '',
    toResultMonthRevenue: '',
    fromRevenue: '',
    toRevenue: '',
    fromSalarySalesAmount: '',
    toSalarySalesAmount: '',
    fromArrearsAmount: '',
    toArrearsAmount: '',
    fromSalaryTotalAmount: '',
    toSalaryTotalAmount: ''
  }
  query = {
    branches: {
      i: 0,
      ps: 1000,
      search: '',
      type: 2
    },
    offices: {
      i: 0,
      ps: 1000,
      search: '',
      type: 3
    },
    members: {
      i: 0,
      ps: 20,
      search: '',
      statusAccount: 0,
      groupCode: 'INSURANCE',
      sort: 1
    }
  }
  saleChanelList = []
  contractStatusList = []

  customerTypeList = []
  consutanList = []
  expiredStatus = []
  paymentPeriodList = [
    {
      value: '1',
      lable: '1'
    },
    {
      value: '2',
      lable: '2'
    },
    {
      value: '4',
      lable: '4'
    }
  ]

  paginitInfo = {
    i: 0,
    ps: 20,
    limit: '',
    page: 1,
    search: '',
    statusAccount: 0,
    groupCode: 'INSURANCE',
    sort: 1
  }
  areaList = []
  branchList: any = []
  employeeList = []
  transactionList: any = []

  loadingAll = false

  queryArea: Record<string, any> = {
    type: 1
  }

  queryBranch: Record<string, any> = {
    type: 2,
    search: '',
    i: 0,
    ps: 10000
  }
  queryOffice: Record<string, any> = {
    type: 3,
    search: '',
    i: 0,
    ps: 10000
  }
  queryEmployee: Record<string, any> = {
    i: 0,
    ps: 20,
    search: '',
    statusAccount: 0,
    groupCode: 'INSURANCE'
  }

  listFilter: Record<string, any> = {
    member: [],
    branch: [],
    offfice: []
  }

  // NumOfCancel
  // ResultMonthRevenue
  // Revenue
  // SalarySalesAmount
  // ArrearsAmount
  // SalaryTotalAmount

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  convertNumberToDecimal(value: string): void {
    this.dataForm[value] =
      //@ts-ignore
      this.$options.filters.formatNumber(this.convertDecimalToNumber(this.dataForm[value])) === '0'
        ? null
        : //@ts-ignore
          this.$options.filters.formatNumber(this.convertDecimalToNumber(this.dataForm[value]))
  }

  convertDecimalToNumber(number: string): number {
    return Number(number.replaceAll(',', ''))
  }
  loadMoreBranch(): void {
    this.query.branches.i += 20
    const a = debounce(this.getBranch, 100)
    a(this.query.branches.search, true)
  }

  remoteBranch(query: string): void {
    this.query.branches.search = trim(query)
    this.query.branches.i = 0
    this.query.branches.ps = 20
    const a = debounce(this.getBranch, 500)
    a(query)
  }

  async getBranch(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.locationRes?.list('branch', {
        search: trim(search),
        i: this.query.branches.i,
        ps: this.query.branches.ps
      })
      if (isAdd) {
        const a = result || []
        this.branchList = [...this.branchList, ...a]
      } else {
        this.branchList = result || []
      }
      if (this.getDisableArea) {
        // @ts-ignore
        // const isMember = this.role.userRole.length === 1 && this.role.userRole.find(role => role === 'TRANSACTION_OFFICE_MEMBER')
        if (filter(this.branchList, (item: any) => item.id === this.user.branchId).length) {
          this.dataForm.branchIds.push(this.user.branchId)
        } else {
          const item = {
            id: this.user.branchId,
            name: this.user.branchName,
            locationCode: this.user.branchCode
          }
          this.branchList.push(item)
          this.dataForm.branchIds.push(this.user.branchId)
        }
      }
    } catch (error) {
      this.isLoading = false
      this.branchList = []
    }
  }
  handleChangeInput(type: string, value: string): void {
    this.convertNumberToDecimal(value)
    if (type === 'numOfContract') {
      if (
        this.dataForm.toNumOfContract &&
        this.dataForm.fromNumOfContract &&
        this.convertDecimalToNumber(this.dataForm.toNumOfContract) < this.convertDecimalToNumber(this.dataForm.fromNumOfContract)
      ) {
        this.listError.numOfContract = true
      } else {
        this.listError.numOfContract = false
      }
    }
    if (type === 'numOfCancel') {
      if (
        this.dataForm.toNumOfCancel &&
        this.dataForm.fromNumOfCancel &&
        this.convertDecimalToNumber(this.dataForm.toNumOfCancel) < this.convertDecimalToNumber(this.dataForm.fromNumOfCancel)
      ) {
        this.listError.numOfCancel = true
      } else {
        this.listError.numOfCancel = false
      }
    }
    if (type === 'sesultMonthRevenue') {
      if (
        this.dataForm.toResultMonthRevenue &&
        this.dataForm.fromResultMonthRevenue &&
        this.convertDecimalToNumber(this.dataForm.toResultMonthRevenue) < this.convertDecimalToNumber(this.dataForm.fromResultMonthRevenue)
      ) {
        this.listError.sesultMonthRevenue = true
      } else {
        this.listError.sesultMonthRevenue = false
      }
    }
    if (type === 'revenue') {
      if (this.dataForm.toRevenue && this.dataForm.fromRevenue && this.convertDecimalToNumber(this.dataForm.toRevenue) < this.convertDecimalToNumber(this.dataForm.fromRevenue)) {
        this.listError.revenue = true
      } else {
        this.listError.revenue = false
      }
    }
    if (type === 'salarySalesAmount') {
      if (
        this.dataForm.toSalarySalesAmount &&
        this.dataForm.toSalarySalesAmount &&
        this.convertDecimalToNumber(this.dataForm.toSalarySalesAmount) < this.convertDecimalToNumber(this.dataForm.fromSalarySalesAmount)
      ) {
        this.listError.salarySalesAmount = true
      } else {
        this.listError.salarySalesAmount = false
      }
    }
    if (type === 'arrearsAmount') {
      if (
        this.dataForm.toArrearsAmount &&
        this.dataForm.fromArrearsAmount &&
        this.convertDecimalToNumber(this.dataForm.toArrearsAmount) < this.convertDecimalToNumber(this.dataForm.fromArrearsAmount)
      ) {
        this.listError.arrearsAmount = true
      } else {
        this.listError.arrearsAmount = false
      }
    }
    if (type === 'salaryTotalAmount') {
      if (
        this.dataForm.toSalaryTotalAmount &&
        this.dataForm.fromSalaryTotalAmount &&
        this.convertDecimalToNumber(this.dataForm.toSalaryTotalAmount) < this.convertDecimalToNumber(this.dataForm.fromSalaryTotalAmount)
      ) {
        this.listError.salaryTotalAmount = true
      } else {
        this.listError.salaryTotalAmount = false
      }
    }
  }

  resetQuery(type: string): void {
    if (type === 'branch') {
      this.queryBranch.i = 0
      this.queryBranch.ps = 10000
    }
    if (type === 'office') {
      this.queryOffice.i = 0
      this.queryOffice.ps = 10000
    }
    if (type === 'member') {
      this.queryEmployee.i = 0
      this.queryEmployee.ps = 20
    }
  }
  remoteArea(query: string): void {
    const temp = {
      ...this.queryArea,
      search: trim(query)
    }
    apiUser
      .getListFilterSalary(temp, this.districtId)
      .then(list => {
        this.areaList = list.data
      })
      .catch(error => console.log(error))
  }

  remoteMembers(query: string): void {
    this.resetQuery('member')
    const temp = {
      ...this.queryEmployee,
      search: trim(query)
    }
    this.apiEmployee
      .getEmployeeList(temp)
      .then(list => (this.members = list.data))
      .catch(error => console.log(error))
  }

  loadArea(): void {
    // this.queryArea.i += 10
    // this.getAreaList()
  }

  async getAreaList(): Promise<void> {
    const temp = {
      ...this.queryArea,
      districtId: this.districtId
    }
    await apiUser
      .getAreaList(temp)
      .then((res: any) => {
        this.areaList = res.data
        if (this.getDisableArea) {
          // @ts-ignore
          this.dataForm.areaIds.push(this.user.areaId)
        }

        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
  }

  loadMember(): void {
    this.queryEmployee.i += 20
    this.getEmployeeList()
  }

  async getEmployeeList(): Promise<void> {
    await this.apiEmployee
      .getEmployeeList(this.queryEmployee)
      .then(data => {
        this.members = [...this.members, ...data.data]
        if (this.getDisableSaleMember) {
          if (filter(this.members, (item: any) => item.id === this.user.memberId).length) {
            this.dataForm.salesMemberIds.push(this.user.memberId)
          } else {
            const item = {
              id: this.user.memberId,
              fullName: this.user.fullName,
              memberNo: this.user.memberNo
            }
            this.members.push(item)
            this.dataForm.salesMemberIds.push(this.user.memberId)
          }
        }
      })
      .catch(() => {
        this.isLoading = false
        this.loadingAll = false
      })
  }
  loadMoreTransactionOffice(): void {
    this.query.offices.i += 20
    this.query.offices.ps = 20
    const a = debounce(this.getTransactionOffice, 100)
    a(this.query.offices.search, true)
  }

  remoteTransactionOffice(query: string): void {
    this.query.offices.search = trim(query)
    this.query.offices.i = 0
    this.query.offices.ps = 20
    const a = debounce(this.getTransactionOffice, 500)
    a(query)
  }

  async getTransactionOffice(search: string, isAdd = false): Promise<any> {
    try {
      const result = await await this.locationRes?.list('office', {
        search: trim(search),
        i: this.query.offices.i,
        ps: this.query.offices.ps
      })
      if (isAdd) {
        const a = result || []
        this.transactionList = [...this.transactionList, ...a]
      } else {
        this.transactionList = result || []
      }
      if (this.getDisableTransaction) {
        // @ts-ignore
        // const isMember = this.role.userRole.length === 1 && this.role.userRole.find(role => role === 'TRANSACTION_OFFICE_MEMBER')
        if (filter(this.transactionList, (item: any) => item.id === this.user.transactionOfficeId).length) {
          this.dataForm.transOfficeIds.push(this.user.transactionOfficeId)
        } else {
          const item = {
            id: this.user.transactionOfficeId,
            name: this.user.transactionOfficeName,
            locationCode: this.user.transactionOfficeCode
          }
          this.transactionList.push(item)
          this.dataForm.transOfficeIds.push(this.user.transactionOfficeId)
        }
      }
    } catch (error) {
      this.isLoading = false
      this.transactionList = []
    }
  }
  async handleOpen(): Promise<any> {
    this.loadingAll = true
    const filters = this.getFilter(this.$route?.name as string)
    await Promise.all([this.getAreaList(), this.getTransactionOffice(''), this.getBranch(''), this.getEmployeeList()]).then(() => {
      if (filters) {
        this.dataForm = Object.assign(this.dataForm, filters.filters)
        if (filters.filters.salesMemberIds?.length) {
          this.handlePushSaleMember(filters.filters.salesMemberIds)
        }
        console.log(this.dataForm)
      }
      this.loadingAll = false
    })
  }

  async handlePushSaleMember(ids: number[]): Promise<void> {
    const promises: any[] = []
    for (let i = 0; i < ids.length; i++) {
      promises.push(this.getEmployee(ids[i]))
    }
    const rs = await Promise.all(promises)
    this.members = [...this.members, ...rs]
  }

  async getEmployee(id: number): Promise<any> {
    return await this.apiEmployee.getEmployeeList({ search: id }, true)
  }

  resetQueryAll(): void {
    this.queryBranch = {
      type: 2,
      search: '',
      i: 0,
      ps: 1000
    }
    this.queryOffice = {
      type: 3,
      search: '',
      i: 0,
      ps: 1000
    }
    this.queryEmployee = {
      i: 0,
      ps: 20,
      search: '',
      statusAccount: 0,
      groupCode: 'INSURANCE'
    }
  }

  handleClose(): void {
    this.resetQueryAll()
    this.branchList = []
    this.transactionList = []
    this.members = []
    // @ts-ignore
    this.$refs.filterForm.clearValidate()
    this.dataForm.fromNumOfContract = ''
    this.dataForm.toNumOfContract = ''
    this.dataForm.fromNumOfCancel = ''
    this.dataForm.toNumOfCancel = ''
    this.dataForm.fromResultMonthRevenue = ''
    this.dataForm.toResultMonthRevenue = ''
    this.dataForm.fromRevenue = ''
    this.dataForm.toRevenue = ''
    this.dataForm.fromSalarySalesAmount = ''
    this.dataForm.toSalarySalesAmount = ''
    this.dataForm.fromArrearsAmount = ''
    this.dataForm.toArrearsAmount = ''
    this.dataForm.fromSalaryTotalAmount = ''
    this.dataForm.toSalaryTotalAmount = ''

    this.areaList = []
    if (this.getDisableArea) {
      this.dataForm.areaIds = []
      this.dataForm.branchIds = []
    }
    if (this.getDisableSaleMember) {
      this.dataForm.salesMemberIds = []
    }
    if (this.getDisableTransaction) {
      this.dataForm.transOfficeIds = []
    }
  }
  handleSave(): void {
    this.setReportFilter({
      contract: this.$route?.name as string,
      filters: {
        ...this.dataForm
      }
    })
    let checkListError = false
    for (const property in this.listError) {
      if (this.listError[property] == true) {
        checkListError = true
        break
      }
    }
    let form: any = this.$refs.filterForm
    form.validate((valid: any) => {
      if (valid && !checkListError) {
        const data: Record<string, any> = {
          ...this.dataForm,
          fromNumOfContract: this.dataForm.fromNumOfContract ? this.convertDecimalToNumber(this.dataForm.fromNumOfContract) : null,
          toNumOfContract: this.dataForm.toNumOfContract ? this.convertDecimalToNumber(this.dataForm.toNumOfContract) : null,
          fromNumOfCancel: this.dataForm.fromNumOfCancel ? this.convertDecimalToNumber(this.dataForm.fromNumOfCancel) : null,
          toNumOfCancel: this.dataForm.toNumOfCancel ? this.convertDecimalToNumber(this.dataForm.toNumOfCancel) : null,
          fromResultMonthRevenue: this.dataForm.fromResultMonthRevenue ? this.convertDecimalToNumber(this.dataForm.fromResultMonthRevenue) : null,
          toResultMonthRevenue: this.dataForm.toResultMonthRevenue ? this.convertDecimalToNumber(this.dataForm.toResultMonthRevenue) : null,
          fromRevenue: this.dataForm.fromRevenue ? this.convertDecimalToNumber(this.dataForm.fromRevenue) : null,
          toRevenue: this.dataForm.toRevenue ? this.convertDecimalToNumber(this.dataForm.toRevenue) : null,
          fromSalarySalesAmount: this.dataForm.fromSalarySalesAmount ? this.convertDecimalToNumber(this.dataForm.fromSalarySalesAmount) : null,
          toSalarySalesAmount: this.dataForm.toSalarySalesAmount ? this.convertDecimalToNumber(this.dataForm.toSalarySalesAmount) : null,
          fromArrearsAmount: this.dataForm.fromArrearsAmount ? this.convertDecimalToNumber(this.dataForm.fromArrearsAmount) : null,
          toArrearsAmount: this.dataForm.toArrearsAmount ? this.convertDecimalToNumber(this.dataForm.toArrearsAmount) : null,
          fromSalaryTotalAmount: this.dataForm.fromSalaryTotalAmount ? this.convertDecimalToNumber(this.dataForm.fromSalaryTotalAmount) : null,
          toSalaryTotalAmount: this.dataForm.toSalaryTotalAmount ? this.convertDecimalToNumber(this.dataForm.toSalaryTotalAmount) : null,
          areaIds: this.dataForm.areaIds.length ? this.dataForm.areaIds.join(',') : null,
          branchIds: this.dataForm.branchIds.length ? this.dataForm.branchIds.join(',') : null,
          transOfficeIds: this.dataForm.transOfficeIds.length ? this.dataForm.transOfficeIds.join(',') : null,
          salesMemberIds: this.dataForm.salesMemberIds.length ? this.dataForm.salesMemberIds.join(',') : null
        }

        this.$emit('apply', data)
        this.setOpenPopup({
          popupName: 'filter-salary',
          isOpen: false
        })
      } else {
        this.$message.error('Nhập chính xác thông tin!')
      }
    })
  }
  created(): void {
    this.apiEmployee = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.api = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })
  }

  resetData(): void {
    //@ts-ignore
    this.$refs.filterForm.clearValidate()
    let newListError = mapValues(this.listError, () => false)
    this.listError = newListError
    this.dataForm = {
      ...this.dataForm,
      toSalaryTotalAmount: null,
      fromSalaryTotalAmount: null,
      toArrearsAmount: null,
      fromArrearsAmount: null,
      toSalarySalesAmount: null,
      fromSalarySalesAmount: null,
      toRevenue: null,
      fromRevenue: null,
      toResultMonthRevenue: null,
      fromResultMonthRevenue: null,
      toNumOfCancel: null,
      fromNumOfCancel: null,
      toNumOfContract: null,
      fromNumOfContract: null,
      month: new Date().getMonth() + 1,
      year: new Date().getFullYear() + ''
    }

    this.checkResetData()
  }
  checkResetData(): void {
    if (!this.getDisableArea) {
      this.dataForm.areaIds = []
      this.dataForm.branchIds = []
    }
    if (!this.getDisableSaleMember) {
      this.dataForm.salesMemberIds = []
    }
    if (!this.getDisableTransaction) {
      this.dataForm.transOfficeIds = []
    }
  }
}
